/* Notification-------------------------------------------------------------------------------------------------- */
function notificationSuccess() {
  let notificationSuccess = document.getElementById("notificationSuccess");
  notificationSuccess.style.display = "block";
  setTimeout(function () {
    notificationSuccess.style.display = "none";
  }, 5000);
}
