function DisplayTools(categorie, liste) {
  ///json
  //recuperer JSON catégorie codes
  if (document.querySelector(liste)) {
    let ul = document.querySelector(liste);
    //console.log(liste);
    fetch('assets/data/list.json')
      .then(response => response.json())
      .then(json => {
        //console.log(list);
        json.forEach((data) => {
          //récuperer tous les outils de la catégorie codes
          //var categorieFiltre = "design";
          let data_id = data.id;
          let data_titre = data.titre;
          let data_description = data.description_fr;
          let data_logo = data.logo;
          let data_logoretina = data.logoretina;
          let data_categorie = data.categorie;
          let data_link = data.link;

          //transformer tout en minuscules et filtrer les outils
          if (data.categorie.toLowerCase() == categorie) {
            //console.log(data);
            //selectionner les ul


            //selectionner les li
            const li = document.createElement('li');
            li.classList.add('codes__el');

            li.innerHTML = `
         <div class="box is-relative">
          <div class="codes__el-content">
              <div class="columns">
                  <div class="column is-3">

                      <img class="image image--tool has-text-centered-touch center"
                          src="${data_logo}" alt="Logo de ${data_titre}"
                          srcset="${data_logo} 1x, ${data_logoretina} 2x"> 
              

                  </div>
                  <div class="column is-8 ">
                      <p class="subtitle is-4 has-text-centered-touch">
                          <strong>${data_titre}</strong>
                      </p>
                      <p>
                        ${data_description}
                      </p>
                      <hr>
                  </div>
                
              </div>
              
              <div class="columns is-flex-tablet is-multilines has-text-centered-touch has-text-left">
                  
                  <div class="column is-3 is-offset-3 is-3-desktop is-offset-3-desktop is-2-widescreen ">
                      <a class="button is-outline" >
                        <span class="button__favoris" data-id="${data_id}">
                            <span>Favoris</span>
                        </span>
                      </a>

                      

                  </div>
                  <div class="column is-0">
                      <a href="${data_link}" target="_blank" class="button button__link button--dark">

                          <span>Découvrir</span>
                          <span class="icon">
                              <i class="fas fa-long-arrow-alt-right"></i>
                              <!-- <i class="fas fa-chevron-right"></i> -->
                          </span>
                      </a>
                  </div>
              </div>
            </div>
        </div>
         `;
            //ajouter les enfants li au parent ul
            ul.appendChild(li);
            /* ajouter publicité ici (dans un li) */
          }
          else {
            return;
          }

        });




        const triggers = document.querySelectorAll(".button__favoris");
        for (let i = 0; i < triggers.length; i++) {
          // au chargement de la page, on remet si nécessaire la class button__favoris-checked 
          if (isInFavorites(triggers[i].getAttribute("data-id"))) {
            triggers[i].classList.add("button__favoris-checked");
          }
          // Au click ...
          triggers[i].addEventListener("click", (e) => {
            // On récupère l'attribut data-id de l'élément qui vient d'être cliqué.
            let id = e.currentTarget.getAttribute("data-id");

            // Si l'élément ne fait pas partie des favoris
            if (!isInFavorites(id)) {
              // On l'ajoute l'élément dans la liste favoris
              addFavorites(id);
              //console.log("id du boutton cliqué " + id);
              // ... et on lui ajoute la classe button__favoris-checked
              e.currentTarget.classList.add("button__favoris-checked");
            }
            else {
              // On enlève l'élément au favoris
              removeFromFavorites(id);
              // On lui enléve la classe button__favoris-checked
              e.currentTarget.classList.remove("button__favoris-checked");
            }
          });
        }
      });
  }
  else {
    return;
  }

}


