/////favoris ( module localStorage )////////
////////////////////////////////////////////
////////////////////////////////////////////
////////////////////////////////////////////


// Fonction qui permet d'ajouter un el au favoris
function addFavorites(id) {
  let Favorites = getFavorites();
  if (!isInFavorites(id)) {
    Favorites.push(id);
    localStorage.setItem("Favorites", JSON.stringify(Favorites));

  }
}

// Fonction qui permet d'enlever un el des favoris
function removeFromFavorites(id) {
  let Favorites = getFavorites();
  let updatedList = Favorites.filter((value) => { return value != id });
  localStorage.setItem("Favorites", JSON.stringify(updatedList));
}

// Fonction qui permet de vérifier si un id est dans les favoris
function isInFavorites(id) {
  let Favorites = getFavorites();
  let element = Favorites.find((value) => { return value == id });
  return element !== undefined;
}

// Retourne la liste des favoris sous la forme d'un tableau
function getFavorites() {
  let FavoritesJSON = localStorage.getItem("Favorites");
  let Favorites = [];
  if (FavoritesJSON === null) {
    localStorage.setItem("Favorites", JSON.stringify(Favorites));
  } else {
    Favorites = JSON.parse(FavoritesJSON);
  }
  return Favorites;

}



function myFavorites() {
  const listeFavoris = document.querySelector('.favoris');
  let url = 'assets/data/list.json';
  fetch(url).then(response => response.json())
    .then(json => {
      json.forEach((data) => {
        //console.log(isInFavorites(data.id));
        //console.log(liste.indexOf("1"));
        if (isInFavorites(data.id)) {

          const li = document.createElement('li');
          //affecter id  à li
          li.classList.add('favoris__el');
          li.setAttribute("data-id", data.id);
          li.innerHTML = `
             <div class="box is-relative ">
              <div class="favoris__el-content">
                  <div class="columns">
                      <div class="column is-3">
                        
                          <img class="image image--tool has-text-centered-touch center"
                              src="${data.logo}" alt="Logo de ${data.titre}"
                              srcset="${data.logo} 1x, ${data.logoretina} 2x"> 
                  
    
                      </div>
                      <div class="column is-8 ">
                          <p class="subtitle is-4 has-text-centered-touch">
                              <strong>${data.titre}</strong>
                          </p>
                          <p>
                            ${data.description_fr}
                          </p><hr>
                      </div>
                    
                  </div>
                  
                  <div class="columns is-flex-tablet is-multilines has-text-centered-touch has-text-left">
                      
                      <div class="column is-3 is-offset-3 is-3-desktop is-offset-3-desktop is-2-widescreen ">
                          <a class="button  is-outline" >
                            <span class="button__favoris button__favoris-checked" data-id="${data.id}">
                                <span>Favoris</span>
                            </span>
                          </a>
    
                          
    
                      </div>
                      <div class="column is-0">
                          <a href="${data.link}" target="_blank" class="button button__link button--dark">
    
                              <span>Découvrir</span>
                              <span class="icon">
                                  <i class="fas fa-long-arrow-alt-right"></i>
                                  <!-- <i class="fas fa-chevron-right"></i> -->
                              </span>
                          </a>
                      </div>
                  </div>
                </div>
            </div>
             `;

          //ajouter li comme enfant pour ul
          listeFavoris.appendChild(li);
        }
        else {

          //return;
        }
      });
      const trig = document.querySelectorAll(".button__favoris");
      for (let i = 0; i < trig.length; i++) {
        // Au click ...
        trig[i].addEventListener("click", (e) => {
          //console.log(e.currentTarget);
          // On récupère l'attribut data-id de l'élément qui vient d'être cliqué.
          let id = e.currentTarget.getAttribute("data-id");
          //console.log("id du boutton cliqué " + id);
          // On enlève l'élément de la liste
          removeFromFavorites(id);
          // On lui enléve la classe button__favoris-checked
          e.currentTarget.classList.remove("button__favoris-checked");
        });
      }
    });
}

